import React, { Component } from 'react'

export default class Create extends Component {
    constructor(){
        super();
        this.state={
            name:null,
            email:null,
            addres:null,
            rating:null
        }
    }
    Create()
    {
        fetch(" http://localhost:3000/Restorant", {
            method:"Post",
            headers:{
                'Content-Type':'application/json'
            },
            body: JSON.stringify(this.state)
        

        }).then((result)=>{
         result.json().then((resp)=>{
alert("Hotel has been Createrd")
         })
        })
    }

    render() {
        return (
            <div  style={{backgroundColor:'#4d5966'}}>
               <h1 style={{fontSize:40 , color:"white"}}>create Hotel</h1> 

        

      <section className="text-gray-600 body-font relative">
        <div className="container px-5 py-24 mx-auto">
          
          <div className="lg:w-1/2 md:w-2/3 mx-auto">
            <div className="flex flex-wrap -m-2">
              <div className="p-2 w-1/2">
                <div className="relative">
                  <label   htmlFor="name" className="leading-7 text-sm text-light-600" >Hotel Name</label>
                  <input placeholder="enter name"  onChange={(e)=>{this.setState({name:e.target.value})}} type="text" id="name" name="name" className="w-full bg-light-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
                </div>
              </div>
              <div className="p-2 w-1/2">
                <div className="relative">
                  <label  htmlFor="name" className="leading-7 text-sm text-gray-600">Hotel Email</label>
                  <input placeholder="enter Email" onChange={(e)=>{this.setState({email:e.target.value})}} type="text" id="name" name="name" className="w-full bg-light-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
                </div>
              </div>
              <div className="p-2 w-1/2">
                <div className="relative">
                  <label  htmlFor="name" className="leading-7 text-sm text-gray-600">Address</label>
                  <input placeholder="enter address" onChange={(e)=>{this.setState({addres:e.target.value})}} type="text" id="name" name="name" className="w-full bg-light-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
                </div>
              </div>
              <div className="p-2 w-1/2">
                <div className="relative">
                  <label  htmlFor="email" className="leading-7 text-sm text-gray-600">Rating</label>
                  <input placeholder="enter rating" onChange={(e)=>{this.setState({rating:e.target.value})}} type="email" id="email" name="email" className="w-full bg-light-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-white focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
                </div>
              </div>
              
              
              <div className="p-2 w-full">
                <button onClick={()=>{this.Create()}} className="flex mx-auto text-white bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg">Create Hotel</button>
              </div>
              
    
            </div>
          </div>
        </div>
      </section>


             
            </div>
        )
    }
}
