import React, { Component } from 'react'
import { Table } from 'react-bootstrap'
import{Link} from "react-router-dom" 
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit ,faTrashAlt} from '@fortawesome/free-solid-svg-icons'

export default class List extends Component {
    constructor() {
        super()
        this.state = {
            list: null
        }
    }
    componentDidMount() {
       this.getData()
    }
    getData()
    {
      fetch("http://localhost:3000/Restorant").then((response) => {
        response.json().then((result) => {

            this.setState({ list: result })
        })
    })
    }
    Delet(id)
    {
      fetch(" http://localhost:3000/Restorant/"+id, {
        method:"DELETE",
       
    

    }).then((result)=>{
     result.json().then((resp)=>{
      this.getData()
alert("Hotel has been delete")

     })
    })
    }
    render() {
        return (
            <div style={{backgroundColor:'#4d5966'}}>
                      <h1 style={{fontSize:30, color:"white"}}>List Of Hotels </h1>
                
              {
                    this.state.list ?
                        <div> {this.state.list.map((item, i) =>

                            <div style={{ fontSize:25, margin: '15px', padding: '20px' ,display:'inline-block' }} >
               
      <section className="text-gray-600 body-font">
        <div className="container px-5 py-24 mx-auto">
          <div className="flex flex-col text-center w-full mb-20">
            <h1 className="sm:text-3xl text-2xl font-medium title-font mb-4 text-gray-900"></h1>
            <p className="lg:w-2/3 mx-auto leading-relaxed text-base"></p>
          </div>
          <div className="flex flex-wrap -m-2">
            
              <div style={{marginTop:-190 , backgroundColor:"white"}} className="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                <div className="flex-grow" >
                  <h1 style={{textAlign:"right"}}>{i+1}</h1>
                  <h2 className="text-gray-900 title-font font-medium"  style={{textAlign:'left' }} > Name:  {item.name}<br/>Email: {item.email}<br/>  Address: {item.addres}<br/>Rating: {item.rating}<br/><div style={{textAlign: 'right', color:'blue' }}>{<Link to ={'/update/'+item.id}><FontAwesomeIcon  icon={faEdit} /> </Link> }<span onClick={()=>this.Delet(item.id)}><FontAwesomeIcon  icon={faTrashAlt} color="red" /></span></div></h2>
                  <p className="text-gray-500"></p>
                </div>
              </div>
             </div>
        </div>
      </section>
                            </div>
                        )}   </div>
                        : <p>Please wait....</p>
                } 
                              
          
              
     
         

         

          
           
                                
                               
                      
       
 



            </div>
        )
    }
}
